@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
.footer {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .footer-text {
    color: var(--white);
    text-align: center;
    font-size: 1.5rem;
  }
  
  .footer-text span{
    color: var(--orange);
    font-size: 2rem;
  }
:root {
  --white: #fff;
  --black: #222;
  --blueLight: #94C6F2;
  --blackLight: #121E26;
  --yellow: #F2C53D;
  --orangeLight: #F2B138;
  --orange: #F29D35;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
  scroll-behavior: smooth;
}

*,::before, ::after {
  box-sizing: initial;
}

body {
  box-sizing: inherit;
  font-size: 1.6rem;
  background-color: #121E26;
  background-color: var(--blackLight);
  font-family: 'Lato', sans-serif;
}

h1 {
  font-size: 2rem;
  color: #fff;
  color: var(--white);
  text-align: center;
}

img {
  width: 100%;
  height: auto;
}

form {
  width: 100%;
}

a {
  outline: none;
  text-decoration: none;
  background-color: #F2B138;
  background-color: var(--orangeLight);
  color: #222;
  color: var(--black);
  border: 1px solid #F2C53D;
  border: 1px solid var(--yellow);
  border-radius: 3px;
  padding: 0.5rem 4rem;
  margin: 0.25rem auto;
  cursor: pointer;
}

a:hover {
  background-color: #F29D35;
  background-color: var(--orange);
  color: #fff;
  color: var(--white);
  transition: 0.25s;
}

button {
  outline: none;
  background-color: #F2B138;
  background-color: var(--orangeLight);
  color: #222;
  color: var(--black);
  border: 1px solid #F2C53D;
  border: 1px solid var(--yellow);
  border-radius: 3px;
  padding: 0.5rem 4rem;
  margin: 0.25rem auto;
  cursor: pointer;
  font-size: 1.6rem;
}

button:hover {
  background-color: #F29D35;
  background-color: var(--orange);
  color: #fff;
  color: var(--white);
  transition: 0.25s;
}

select {
  outline: none;
  padding: 0.5rem 2rem;
  border: 1px solid #94C6F2;
  border: 1px solid var(--blueLight);
  background-color: #fff;
  background-color: var(--white);
  border-radius: 3px;
  width: 100%;
  margin: 0.25rem 0;
  cursor: pointer;
}

select:hover {
  box-shadow: 0 0 1px 1px;
}

input {
  outline: none;
  border: 1px solid #94C6F2;
  border: 1px solid var(--blueLight);
  border-radius: 3px;
  padding: 0.5rem 2rem;
  width: 100%;
  margin: 0.25rem 0;
}

.container {
  max-width: min(90%, 192rem);
  margin: 0 auto;
}
.container-logo {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo {
    width: 25rem;
    -webkit-filter: drop-shadow(1px 0px 1px var(--black));
            filter: drop-shadow(1px 0px 1px var(--black));
}
.App {
  display: grid;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

@media (min-width: 768px){
    form {
      width: 60rem;
    }
}

.container-meme-result {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  padding-top: 1rem;
}

@media (min-width: 768px){
  .container-meme-result {
    flex-direction: row;
    padding-top: 0;
  }
}

.container-meme {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items:flex-end;
  position: relative;
  height: 20rem;
  width: 20rem;
}

.meme__items{
  z-index: 100;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items:center;
  height: 100%;
  width: 100%;
  text-overflow: clip;
  word-break: break-all;
}

.meme-top-text, .meme-bottom-text {
  font-size: 2.4rem;
  color: var(--white);
  text-shadow: 0 5px 5px var(--black);
  font-weight: 700;
  font-family: 'Lato', sans-serif;
  text-align: center;
}

.meme-img {
  border-radius: 3px;
  position: absolute;
  object-fit: cover;
  z-index: 1;
}

.icon-arrow-down {
  color: var(--white);
  font-size: 4.8rem;
}

.icon-arrow-right {
  display: none;
}

@media (min-width: 768px){
  .icon-arrow-right {
    display: block;
    color: var(--white);
    font-size: 4.8rem;
  }
  
  .icon-arrow-down {
    display: none;
  }
}


.meme-result {
  width: 20rem;
  height: auto;
  margin: 2rem 0;
  box-shadow: 0 0 5px 10px var(--orange);
  height: 20rem;
}

.meme-download {
  display: none;
}


